<template>
    <div>
        <el-table :data="tableData"
                  stripe
                  style="width: 100%">
            <el-table-column>
                <el-table-column
                        prop="company"
                        label=""
                ></el-table-column>
                    <el-table-column
                        prop="name"
                        label=""
                ></el-table-column>
                <el-table-column
                        prop="id"
                        label=""
                ></el-table-column>
                <el-table-column
                        prop="gender"
                        label=""
                ></el-table-column>
                <el-table-column
                        prop="jobPosition"
                        label=""
                ></el-table-column>
                <el-table-column
                        prop="jobTitle"
                        label=""
                >
                </el-table-column>
                <el-table-column
                        prop="type"
                        label=""
                ></el-table-column>
                <el-table-column
                        prop="area"
                        label=""
                ></el-table-column>
                <el-table-column
                        prop="phone"
                        label=""
                ></el-table-column>
                <el-table-column
                        prop="secret"
                        label=""
                ></el-table-column>
                <el-table-column
                        prop="secretLevel"
                        label=""
                ></el-table-column>
                <el-table-column
                        prop="address"
                        label=""
                        width="200"
                ></el-table-column>
                <el-table-column
                        prop="postcode"
                        label=""
                        width="50"
                ></el-table-column>
                    <el-table-column
                            prop="recommendCompany"
                            label=""
                    ></el-table-column>
                    <el-table-column
                            prop="filler"
                            label=""
                    ></el-table-column>
                    <el-table-column
                            prop="fillerContact"
                            label=""
                    ></el-table-column>
                    <el-table-column
                            prop="isBlocked"
                            label=""
                    ></el-table-column>
                <el-table-column
                        fixed="right"
                        label=""
                        width="100">
                    <template slot-scope="scope">
                        <el-button round @click="handleClick(scope.row)" ></el-button>
<!--                        <el-button round></el-button>-->
                    </template>
                </el-table-column>
            </el-table-column>
        </el-table>
      <div style="text-align: center; margin-top: 10px">
        <el-button type="primary" icon="el-icon-user-solid" @click="dumpAsExcel"></el-button>
        <el-button type="primary" icon="el-icon-user-solid" @click="uploadExcel"></el-button>
        <el-button type="primary" icon="el-icon-user-solid" @click="blockList"></el-button>
      </div>
      <el-dialog
          title=""
          :visible.sync="blockedExpert"
          width="500px">
        <p>(ID) - </p>
        <p v-for="blockedExpert in blocked" :key="blockedExpert.id">
          {{blockedExpert.id}} - {{blockedExpert.name}}
        </p>
      </el-dialog>
      <el-dialog
          title=""
          :visible.sync="dialogVisible"
          width="500px">
        <div style="text-align: center">
          <el-upload
              class="upload-demo"
              drag
              :with-credentials="true"
              action="http://localhost:8080/file/importexcel"
              multiple>
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><em></em></div>
            <div class="el-upload__tip" slot="tip">xls/xlsx</div>
          </el-upload>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="afterUpload"> </el-button>
        </span>
      </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "list",
        created: function () {
        this.loadData()
        },
        methods: {
            loadData: function () {
                this.$http.get('http://localhost:8080/expert/get_all').then((res) => {
                    this.tableData = res.data
                    for (var i = 0; i < this.tableData.length; i++) {
                        // if (this.tableData[i].secret) this.tableData[i].secret=''
                        // else this.tableData[i].secret=''
                        var d = new Date(this.tableData[i].birth)
                        this.tableData[i].birth = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() //+ ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
                    }
                    console.log(res)
                }).catch(function (err) {
                    alert(err)
                })
            },
            handleClick(row){
                this.$router.push({path: '/expertDetail', query: {id: row.id}})
            },
            dumpAsExcel:function() {
              // todo: dowload URL
              window.open("http://localhost:8080/file/downloadexcel", "_blank");
              console.log("dump")
            },
            uploadExcel:function() {
              this.dialogVisible = true
              console.log("upload")
            },
            blockList:function() {
              this.$http.get('http://localhost:8080/expert/get_blocked').then((res) => {
                this.blocked = res.data
                console.log(res)
              }).catch(function (err) {
                alert(err)
              })
              this.blockedExpert = true
            },
            afterUpload:function () {
              location.reload()
            }
        },
        data(){
            return{
                tableData:[],
                search:'',
                dialogVisible: false,
                blockedExpert: false,
                blocked: []
            }
        }
    }
</script>

<style scoped>

</style>
