<template>
    <div>
        <navi></navi>
        <div style="margin-top: 10px">
            <el-form  label-width="100px">
                <el-row :gutter="20">
                    <el-col :span="4">
                        <el-form-item label="">
                            <el-input v-model="numberTech" ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item label="">
                            <el-input v-model="numberMng" ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item label="">
                            <el-input v-model="numberAcc" ></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col :span="4">
                        <el-form-item label="">
                            <el-select v-model="secretFlag" @change="getService" placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <!--                                <el-option label="" value=" "></el-option>-->
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-select v-model="secret" :disabled="secretshow" placeholder="">
                                <el-option
                                        v-for="item in secretoptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-form-item label="" v-for="(val, index) in compNum" :key="index">
                  <el-input v-model="compNum[index]" ></el-input>
                </el-form-item>
                <el-button @click="addCompany" style="margin-left: 100px; margin-top: -10px; margin-bottom: 20px"></el-button><br>
              <div style="margin-left: 100px; margin-top: -10px; margin-bottom: 20px">
              <p></p>
                <input
                    ref="upload"
                    type="file"
                    accept=".xls,.xlsx"
                    class="upload_file"
                    @change="readExcel($event)"
                />
              </div>
                <el-row :gutter="20">
                    <el-col :span="10">
                        <el-form-item label="">
                            <el-date-picker
                                    v-model="startTime"
                                    type="datetime"
                                    placeholder="">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col span="5">
                        <el-date-picker
                                v-model="endTime"
                                type="datetime"
                                placeholder="">
                        </el-date-picker>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <!--                            <el-input v-model="Form.type" ></el-input>-->
                            <el-select v-model="area" placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="9">
                        <el-form-item label="">
                            <el-input v-model="keyword" ></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-form-item>
                    <el-button type="primary" @click="submitForm()"></el-button>
                    <el-button @click="resetForm()"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import navi from "../../components/navi";
import XLSX from "xlsx";
    export default {
        name: "add",
        components: {navi},
        data(){
            return{
                id:'',
                    name:'',
                    numberTech:'',
                    numberMng:'',
                    numberAcc:'',
                    company:'',
                    secret:'',
                    area:'',
                    startTime:'',
                    endTime:'',
                    keyword: '',
                    compNum: [""],
                secretoptions: [{
                    value: '',
                    label: ''
                }, {
                    value: '',
                    label: ''
                }, {
                    value: '',
                    label: ''
                }],
                secretFlag:'',
                secretshow:''
            }
        },
        methods:{
            getService(){
                // this.secretFlag=!this.secretFlag;
                if (this.secretFlag==='') this.secretshow=false;
                else {
                    this.secretshow=true;
                    this.secret=''
                }
                this.$forceUpdate()
            },
            addCompany() {
              this.compNum.push("");
              var compSum = "";
              this.compNum.forEach((c) => {compSum += c + ", "});
              this.company =compSum;
              console.log(this.company)
            },
            resetForm(){
                    this.name='';
                    this.numberTech='';
                    this.numberMng='';
                    this.numberAcc='';
                    this.company='';
                    this.endTime='';
                    this.startTime='';
                    this.secret='';
                    this.secretFlag='';
                    this.secretshow='';
                    this.area='';
                    this.keyword='';
                    this.compNum=["",]
            },
            submitForm(){
                let compSum = "";
                this.compNum.forEach((c) => {compSum += c + ", "});
                // this.secret=this.secret.length?"":this.secret;
                let data = {
                    name: this.name,
                    numberTech:this.numberTech,
                    numberMng:this.numberMng,
                    numberAcc:this.numberAcc,
                    company: compSum,
                    area:this.area,
                    startTime: this.startTime,
                    endTime:this.endTime,
                    keyword: this.keyword,
                    secret: this.secretFlag,
                    secretLevel: this.secret
                };
                console.log(data);
                const url = 'http://localhost:8080/program/insert/';
                this.$http({
                    method: 'post',
                    url: url,
                    headers: {
                        'Access-Control-Allow-Credentials': true,
                        'Access-Control-Allow-Origin': true,
                        'Content-Type': 'application/json'
                    },
                    data: JSON.stringify(data)
                })
                    .then(response => {
                        console.log(response.data);
                        this.id=response.data.id;
                        console.log('get response');
                        //auto choose
                        this.$http.get('http://localhost:8080/program/auto/'+ this.id).then((res) => {
                            // this.tableData = res.data
                            console.log(res.data);
                            if (res.data==="") alert("")
                        }).catch(function (err) {
                            alert(err)
                        });
                        //redirect
                        this.$router.push({path: '/programList'})
                    })
                    .catch(error => {
                        JSON.stringify(error);
                        console.log(error)
                    })

            },
          readExcel(e) {

            console.log("call")
            let that = this;
            const files = e.target.files;
            if (files.length <= 0) {
              return false;
            } else if (!/\.(xls|xlsx)$/.test(files[0].name.toLowerCase())) {
              alert("xlsxlsx")
              return false;
            } else {
              that.upload_file = files[0].name;
            }
            const fileReader = new FileReader();
            fileReader.onload = ev => {
              try {
                const data = ev.target.result;
                const workbook = XLSX.read(data, {
                  type: "binary"
                });
                const wsname = workbook.SheetNames[0]; //
                const ws = XLSX.utils.sheet_to_json(workbook.Sheets[wsname]); //json
                var li = []
                for (var company in ws) {
                  li.push(Object.values(ws[company])[0])
                }
                console.log(li)
                that.compNum = li;
              } catch (e) {
                return false;
              }
            };
            fileReader.readAsBinaryString(files[0]);
          }
        }
    }
</script>

<style scoped>

</style>