<template>
    <div>
        <navi></navi>
        <div style="margin-top: 10px">
            <el-form  label-width="100px">
                <el-row :gutter="20">
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-input v-model="name" ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-input v-model="id" ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item label="">
                            <el-select v-model="secretFlag" @change="getService" placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <!--                                <el-option label="" value=" "></el-option>-->
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-select v-model="secret" :disabled="secretshow" placeholder="">
                                <el-option
                                        v-for="item in secretoptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6" style="align-items: end">
                        <el-form-item label="">
                            <el-input v-model="phone" ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6" style="align-items: end">
                        <el-form-item label="">
                            <el-select v-model="type" multiple clearable placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item label="">
                            <el-select v-model="gender" clearable placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=" "></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                  <el-col :span="4">
                    <el-form-item label="">
                      <el-select v-model="blocked" clearable placeholder="">
                        <el-option label="" :value="true"></el-option>
                        <el-option label="" :value="false"></el-option>
                      </el-select>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-form-item label="">
                    <el-input v-model="company" ></el-input>
                </el-form-item>
                <el-row :gutter="20">
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-date-picker
                                    v-model="birth"
                                    type="date"
                                    placeholder="">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <!--                            <el-input v-model="Form.type" ></el-input>-->
                            <el-select v-model="firstArea" filterable placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-select v-model="secondaryArea" filterable multiple placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="9">
                        <el-form-item label="">
                            <el-input v-model="introduction" ></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-form-item>
                    <el-button type="primary" @click="submitForm()"></el-button>
                    <el-button @click="resetForm()"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    import navi from "../../components/navi";
    export default {
        name: "edit",
        components: {navi},
        data(){
            return{
                    id:'',
                    name:'',
                    phone:'',
                    gender:'',
                    blocked:'',
                    secret:'',
                    company:'',
                    type:'',
                    area:'',
                    introduction:'',
                    birth:'',
                firstArea:'',
                secondaryArea:'',
                secretoptions: [{
                    value: '',
                    label: ''
                }, {
                    value: '',
                    label: ''
                }, {
                    value: '',
                    label: ''
                }],
                value: '',
                secretFlag:'',
                secretshow:''
            }
        },
        mounted: function () {
            this.load()
        },
        methods:{
            getService(){
                // this.secretFlag=!this.secretFlag;
                if (this.secretFlag==='') this.secretshow=false
                else this.secretshow=true
                this.$forceUpdate()
            },
            load() {
                let that = this
                var url = 'http://localhost:8080/expert/get/' + this.$route.query.id
                this.$http.get(url, {
                    headers: {
                        'Access-Control-Allow-Credentials': true,
                        'Access-Control-Allow-Origin': true
                    }
                })
                    .then(res => {
                        // console.log(res.data)
                        that.id = res.data.id
                        that.name = res.data.name
                        that.phone = res.data.phone
                        that.gender = res.data.gender
                        that.birth =res.data.birth
                        that.type = res.data.type
                        that.area = res.data.area   //  
                        let arr=that.area.split(",")
                        that.firstArea=arr.length>=1?arr[0]:''
                        let list=arr.slice(1,arr.length)
                        that.secondaryArea=list
                        that.introduction = res.data.introduction
                        that.company = res.data.company
                        that.secret = res.data.secret
                        if (that.secret.length>0){
                            that.secretFlag=''
                            that.secretshow=false
                        }else{
                            that.secretFlag=''
                            that.secretshow=true
                        }
                        console.log(res.data.store)
                        // console.log(that.isbn)
                    }).catch(error => {
                    JSON.stringify(error)
                    console.log(error)
                })
            },
            resetForm(){
                this.$router.push({path: '/expertDetail', query: {id: this.$route.query.id}})
            },
            setBlocked() {
              if (this.blocked) {
                let url = 'http://localhost:8080/expert/block/' + this.$route.query.id;
                this.$http({
                  method: 'get',
                  url: url,
                  headers: {
                    'Access-Control-Allow-Credentials': true,
                    'Access-Control-Allow-Origin': true,
                    'Content-Type': 'application/json'
                  },
                }).then(response => {
                      console.log(response.data)
                      console.log('get response')
                      return
                    }).catch(error => {
                      JSON.stringify(error)
                      console.log(error)
                      return
                    })
              } else {
                let url = 'http://localhost:8080/expert/unblock/' + this.$route.query.id;
                this.$http({
                  method: 'get',
                  url: url,
                  headers: {
                    'Access-Control-Allow-Credentials': true,
                    'Access-Control-Allow-Origin': true,
                    'Content-Type': 'application/json'
                  },
                }).then(response => {
                  console.log(response.data)
                  console.log('get response')
                  return
                }).catch(error => {
                  JSON.stringify(error)
                  console.log(error)
                  return
                })
              }
            },
            submitForm(){
                // let that = this
                url = 'http://localhost:8080/expert/delete' + this.$route.query.id;
                this.$http({
                    method: 'delete',
                    url: url,
                    headers: {
                        'Access-Control-Allow-Credentials': true,
                        'Access-Control-Allow-Origin': true
                    }
                }).then(response => {
                        console.log(response.data)
                    }).catch(error => {
                        JSON.stringify(error);
                        console.log(error)
                    });
                this.area=this.firstArea+','+this.secondaryArea
               let data = {
                   id:this.$route.query.id,
                    name: this.name,
                    phone: this.phone,
                   gender: this.gender,
                   secret: this.secret,
                   company:this.company,
                   type: this.type.toString(),
                   area:this.area,
                   introduction:this.introduction,
                   birth: this.birth
                }
                var url = 'http://localhost:8080/expert/insert/'
                this.$http({
                    method: 'post',
                    url: url,
                    headers: {
                        'Access-Control-Allow-Credentials': true,
                        'Access-Control-Allow-Origin': true,
                        'Content-Type': 'application/json'
                    },
                    data: JSON.stringify(data)
                })
                    .then(response => {
                        console.log(response.data)
                        console.log('get response')
                        //redirect
                        this.$router.push({path: '/expert'})
                    })
                    .catch(error => {
                        JSON.stringify(error)
                        console.log(error)
                    })
            }
        }
    }
</script>

<style scoped>

</style>