<template>
    <div>
        <navi></navi>
        <div style="margin-top: 10px">
            <el-form  label-width="100px">
                <el-row :gutter="20">
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-input v-model="name" ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-input v-model="id" ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item label="">
                            <el-select v-model="secretFlag" @change="getService" placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
<!--                                <el-option label="" value=" "></el-option>-->
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-select v-model="secret" :disabled="secretshow" placeholder="">
                                <el-option
                                        v-for="item in secretoptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6" style="align-items: end">
                        <el-form-item label="">
                            <el-input v-model="phone" ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6" style="align-items: end">
                        <el-form-item label="">
                            <el-select v-model="type" multiple clearable placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="4">
                        <el-form-item label="">
                            <el-select v-model="gender" clearable placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=" "></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-form-item label="">
                    <el-input v-model="company" ></el-input>
                </el-form-item>
                <el-row :gutter="20">
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-date-picker
                                    v-model="birth"
                                    type="date"
                                    placeholder="">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-select v-model="firstArea" filterable placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-select v-model="secondaryArea" filterable multiple placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="9">
                        <el-form-item label="">
                            <el-input v-model="introduction" ></el-input>
                        </el-form-item>
<!--                        <p>{{firstArea+secondaryArea}}</p>-->
                    </el-col>
                </el-row>
                <el-form-item>
                    <el-button type="primary" @click="submitForm()"></el-button>
                    <el-button @click="resetForm()"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    import navi from "../../components/navi";
    export default {
        name: "edit",
        components: {navi},
        data(){
            return{
                id:'',
                name:'',
                phone:'',
                gender:'',
                secret:'',
                company:'',
                type:'',
                area:'',
                firstArea:'',
                secondaryArea:'',
                introduction:'',
                birth:'',
                secretoptions: [{
                    value: '',
                    label: ''
                }, {
                    value: '',
                    label: ''
                }, {
                    value: '',
                    label: ''
                }],
                value: '',
                secretFlag:'',
                secretshow:''
            }
        },
        methods:{
            getService(){
                // this.secretFlag=!this.secretFlag;
                if (this.secretFlag==='') this.secretshow=false
                else this.secretshow=true
                this.$forceUpdate()
            },
            resetForm(){
                this.birth='',
                    this.id='',
                    this.name='',
                    this.phone='',
                    this.gender='',
                    this.secret='',
                    this.secretFlag='',
                    this.secretshow='',
                    this.company='',
                    this.type='',
                    this.area='',
                    this.firstArea='',
                    this.secondaryArea='',
                    this.introduction='',
                    this.birth=''
            },
            submitForm(){
                // let that = this
                this.area=this.firstArea+','+this.secondaryArea
               let data = {
                   id:this.id,
                    name: this.name,
                    phone: this.phone,
                   gender: this.gender,
                   secret: this.secretFlag,
                   secretLevel: this.secret,
                   company:this.company,
                   type: this.type.toString(),
                   area:this.area,
                   introduction:this.introduction,
                }
                console.log(data);
                var url = 'http://localhost:8080/expert/insert/'
                this.$http({
                    method: 'post',
                    url: url,
                    headers: {
                        'Access-Control-Allow-Credentials': true,
                        'Access-Control-Allow-Origin': true,
                        'Content-Type': 'application/json'
                    },
                    data: JSON.stringify(data)
                })
                    .then(response => {
                        console.log(response.data)
                        console.log('get response')
                        //redirect
                        this.$router.push({path: '/expert'})
                    })
                    .catch(error => {
                        JSON.stringify(error)
                        console.log(error)
                    })
            }
        }
    }
</script>

